$f_primary: 'Roboto';
body{
  font-family: $f_primary !important;
}
.header{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background: linear-gradient(180deg, #000000 0%, #212121 100%);
  box-shadow: 0px 10px 40px rgba(13, 110, 253, 0.2);
  padding: 10px 110px;
  z-index: 99999;
  .desc_linl_pnt{
    margin: 0 auto;
  }
  .header_inn{
    align-items: center;
  }
  .link_prnt{
    display: flex;
    align-items: center;
    a{
      font-family: $f_primary;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      color: #FFFFFF;
      background: transparent;
      box-shadow: none;
      margin-right:55px;
      cursor: pointer !important;
      transition: all 0.2s ease-out;
      &:hover{
        color: #95BFFE;
      }
      &.active{
        color: #95BFFE;
      }
    }
    .last_link{
      margin-right: 0;
    }
  }
  .enterapp_prnt{
    .enterapp_btn{
      width: 147px;
      height: 42px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #0D6EFD;
      border-radius: 60px;
      font-weight: 700;
      font-size: 14px;
      line-height: 16px;
      text-align: center;
      box-shadow: none;
      color: #FFFFFF;
      border: 1px solid #0D6EFD;
      position: relative;
      transition: all 0.2s ease-out;
      &:hover{
        background: #033175;
      }
    }
  }
}

@media screen and (max-width: 1600px) {
  .header {
    padding: 10px 60px;
  }
  .header .link_prnt a {
    margin-right: 30px;
  }
}
@media screen and (max-width: 1199px) {
  .header {
    padding: 10px 30px;
  }
  .header .link_prnt a {
    margin-right: 15px;
    font-size: 13px;
  }
}
@media screen and (min-width: 992px) {
  .mobl_link_prnt{
    display: none;
  }
}
@media screen and (max-width: 991px) {
  .header .link_prnt{
    flex-direction: column;
    padding: 5px;
    a{
      margin-top: 15px;
      margin-right: 0;
    }
    .last_link{
      margin-bottom: 15px;
    }
  }
  .header .nav_callpse{
    position: absolute;
    left: 0;
    top: 72px;
    background: #272d37d3;
    width: 100%;
  }
  .header .collpse_btn {
    border: 2px solid #0D6EFD;
    box-shadow: none;
    background: transparent;
    &:focus{
      box-shadow: none;
    }
    svg{
      color: #0D6EFD;
    }
  }
}
@media screen and (max-width: 599px) {
  .header{
    padding: 10px 20px;
    .collpse_btn_prnt{
      flex: none;
    }
    .main_logo{
      margin: 0 13px;
      height: 40px;
    }
    .main_logo_prnt{
      justify-content: flex-start;
    }
  }
  .header .enterapp_prnt .enterapp_btn {
    width: 110px;
    height: 34px;
    font-size: 13px;
  }
  .header .nav_callpse {
    top: 60px;
  }
  .link_prnt a {
    margin-top: 10px;
  }
  .link_prnt .last_link {
    margin-bottom: 10px;
  }
}
