.switch_box{
  display: flex;
  align-items: center;
  justify-content: center;
  p{
    font-family: $f_primary;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #ffff;
    min-width: 161px;
  }
  .round_switch{
    background: linear-gradient(180deg, #95BFFE 0%, #0D6EFD 100%);
    border-radius: 12.75px;
    width: 45px;
    height: 23px;
    .chakra-switch__track{
      background: linear-gradient(180deg, #95BFFE 0%, #0D6EFD 100%);
      border-radius: 12.75px;
      width: 45px;
      height: 20px;
      align-items: center;
      box-shadow: none !important;
      &:focus{
        box-shadow: none;
      }
      .chakra-switch__thumb[data-checked]{
        position: relative;
        margin-left: 12px;
        box-shadow: none;
      }
    }
  }
  .switch_min{
    width: inherit;
    .switch_text{
      margin: 0;
      margin-right: 15px;
      margin-left: 10px;
      font-family: $f_primary;
      font-weight: 300;
      font-size: 16px;
      line-height: 19px;
      color: #FFFFFF;
    }
  }
}
